
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  emits: {
    input: null,
  },
  setup(props, context) {
    const emitInput = (value: boolean) => context.emit('input', value)
    const onInput = (e: Event) =>
      emitInput((e.target as HTMLInputElement).checked)
    return {
      onInput,
      context,
    }
  },
})
