
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  emits: {
    input: null,
  },
  setup(_, context) {
    const emitInput = (checked: boolean) => {
      if (checked) {
        context.emit('input', true)
      }
    }
    const onInput = (e: Event) =>
      emitInput((e.target as HTMLInputElement).checked)
    return {
      context,
      onInput,
    }
  },
})
